<template>
<div class="selfchoose">
  <div class="scheader">
    <span class="scheader1">名称代码</span><span class="scheader2">最新价</span><span class="scheader3" v-html="con" @click="changeCjl"></span><span class="scheader4">&#xe650;</span>
  </div>
  <div class="scroll" ref="scroll2">
    <div>
  <div class="item" v-for="(item2,index) in dataList" :key="index" >
    <div class="item-name" @click="toDetail(item2)">
      <span class="name">{{item2.name}}</span>
      <span class="ename">{{item2.instrumentid}}</span>
    </div>
    <div class="item-price" @click="toDetail(item2)">
      <span class="price1" :class="[item2.diffPrice>0?'changeRed':(item2.diffPrice<0?'changeGre':'')]">{{item2.NEWPRICE}}{{item2.diffPrice<0?'&#xe7d0;':(item2.diffPrice>0?'&#xe7d2;':'')}}</span>
      <span class="price2" :class="[item2.diffPrice>0?'changeRed':(item2.diffPrice<0?'changeGre':'')]">{{item2.diffPrice>0?item2.diffPrice:item2.diffPrice*(-1)}}/{{item2.diffPriceRate}}</span>
    </div>
    <div class="item-vol" :class="[item2.diffPrice>0?'changeRed':(item2.diffPrice<0?'changeGre':'')]" @click="toDetail(item2)">
      {{con==='成交量'?item2.Vol:(con==='持仓'?item2.Hold:item2.TODAYOPEN)}}
    </div>
<!--    <div class="item-pay">-->
<!--      <div class="pay">-->
<!--        <router-link :to="{name:'pay2',params:{id:buyOrsell2}}"></router-link>-->
<!--        <span @click="toSell(item2)">一键下单</span>-->
<!--      </div>-->
<!--    </div>-->
  </div>
      <div class="add" @click="toAdd">+添加更多</div>
    </div>
  </div>
  <div class="loading" v-show="!tickCopyOK">
    <van-loading  color="#FF7100" size="40px" class="loadingCon"></van-loading>
  </div>
  <transition name="fade">
  <router-view :holdPositions="holdPositions"  @backText="backText" @addSelf="addSelf" @repush="repush" :Tick="Tick" :tickPrice="tickPrice" :tickName="tickName" :ticknameT="tickname" :tickCopy="tickCopy" :openHandM="openHandM" :margin="margin" :DataList="dataList"></router-view>
  </transition>
  <transition name="pay">
  <payy v-if="show" :item2="item2" @back="back" :name="name" :holdPositions="holdPositions"  @backText="backText" @addSelf="addSelf" @repush="repush" :Tick="Tick" :tickPrice="tickPrice" :tickName="tickName" :ticknameT="tickname" :tickCopy="tickCopy" :openHandM="openHandM" :margin="margin" :DataList="dataList"></payy>
  </transition>
  <div class="backTip" v-show="showBack"><div class="bt-content" v-text="btContent"></div></div>
</div>
</template>

<script>
  import Bscroll from 'better-scroll'
  import payy from './pay'
export default {
  data () {
    return {
      con:'成交量',
      // 用户id
      uid:'',
      dataList:null,
      tickPrice:{},
      tickName:'',
      chooseInstrumentid:'',
      tickname:'',       //英文缩写名称
      openHandM:0,
      margin:0,
      tickCopyOK:'',
      btContent:'',
      showBack:false,
      digits:0,
      holdPositions:[],
      item2:{},
      name:'',
      show:false
    }
  },
  props:{
    Tick:{
      type:Object
    },
    tickCopy:{
      type:Object
    },
    reloadSelf:{

    }
  },
  created(){
    // alert(123)
    // console.log(this.$store.state.discUrl)
    let path = this.$route.path;
    path = path.split('/');
    path=path[path.length-1];
    if(path=='market'){
      this.tickCopyOK=false;
    }else {
      this.tickCopyOK=true;
    }
    this.discUrl = localStorage.getItem('discUrl')
    this.uid = localStorage.getItem('uid')
    // console.log(this.uid)
    this.getContract()
    this.getMultiplier()
    // this.$emit('DataList',this.dataList)
  },
  methods: {
    toAdd(){
      this.$router.push({
        name:'addself',
        params:{
          id:'pay1'
        }
      })
    },
    addSelf(){
      this.getContract()
    },
    toSell(item2){
      this.tickName =item2.name;
      this.digits=item2.digits;
      // console.log(item2);
      this.tickname = item2.instrumentid;
      this.$set(this.tickPrice,'price',item2['newPrice']);
      this.show=true;
      if(this.tickCopy.hasOwnProperty(this.tickname)){
        this.tickPrice.bidprice=Number(this.tickCopy[this.tickname].bidprice).toFixed(this.digits);
        this.tickPrice.askprice=Number(this.tickCopy[this.tickname].askprice).toFixed(this.digits);
      }
      console.log(item2);
      this.margin = item2.margin;
      this.openHandM = item2.openHandM;
      this.item2=item2;
      this.name='pay2';
      // this.$router.push({
      //   name:'pay2',
      //   params:{
      //     item:item2
      //   }
      // })
    },
    toDetail (item) {
      // console.log(item);
      this.$router.push({
        name:'detail2',
        params:{
          instrumentId:item.instrumentid,
          name:item.name,
          type:item.type,
          id:'pay3',
          Fentime:item.timeQuantum,
          openHandM:item.openHandM,
          margin:item.margin,
          id2:'save3',
          digits:item.digits
        }
      })
      this.tickCopyOK=true;
    },
    changeCjl(){
      switch (this.con) {
        case "成交量":
          this.con = "持仓";
          break;
        case "持仓":
          this.con = "今开";
          break;
        case "今开":
          this.con = "成交量";
          break;
      }
    },
    back(){
      this.show=!this.show
    },
    ToMoney(num,float){
      let NUM = Number(num).toFixed(float);
      let NumF = Number(NUM).toLocaleString('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: float
      });
      return NumF.substring(1,NumF.length)
    },
    // 获取自选内容
    getContract(){
      this.$axios.get('http://'+this.discUrl+':7777/api/instrumentOptional?userId='+this.uid).then(res =>{
        console.log(res)
        let list={};
        for(let i=0;i<res.data.object.length;i++){
          let listobj = {};
          listobj.name = res.data.object[i].instrumentName;
          listobj.instrumentid = res.data.object[i].instrumentId;
          listobj.timeQuantum=res.data.object[i]['time_quantum'];
          listobj.digits=res.data.object[i].digits;
          listobj.openHandM=res.data.object[i]['commission_open'];
          listobj.margin=res.data.object[i].margin;
          listobj.newPrice = 0;        //最新价
          listobj.diffPrice = 0;      //差价
          listobj.diffPriceRate = 0;   //价格变化率
          listobj.Vol = 0;             //成交量
          listobj.Hold = 0;           //持仓
          listobj.TodayOpen = 0;      //今开
          listobj.NEWPRICE = 0;      //最新价（货币格式）
          listobj.TODAYOPEN = 0;      //今开（货币格式）
          listobj.type = 1;
          list[ listobj.instrumentid]=listobj;
          console.log(list)
        }
        this.dataList=list;
        console.log(this.dataList)
        let timeVal = setInterval(()=>{
          if(this.dataList!==null&&localStorage.tickCopy){
            this.tickCopyOK=true;
            for(let tck in this.tickCopy){
              if(this.dataList.hasOwnProperty(tck)){
                this.dataList[tck].newPrice=this.tickCopy[tck]['price']
                this.dataList[tck].diffPrice=(this.tickCopy[tck]['price']-this.tickCopy[tck]['preclose']).toFixed(this.dataList[tck].digits)
                let diffPriceRate = (this.tickCopy[tck]['price']-this.tickCopy[tck]['preclose'])/this.tickCopy[tck]['preclose'];
                if(diffPriceRate<0){
                  diffPriceRate = diffPriceRate*(-1)
                }
                let NEWPRICE = this.ToMoney(this.tickCopy[tck]['price'],this.dataList[tck].digits);
                let TODAYOPEN = this.ToMoney(this.tickCopy[tck]['preclose'],this.dataList[tck].digits);
                this.dataList[tck].NEWPRICE=NEWPRICE
                this.dataList[tck].diffPriceRate=(diffPriceRate*100).toFixed(2)+'%'
                // this.dataList[tck].Vol=this.tickCopy[tck]['totalvol']>10000?(this.tickCopy[tck]['totalvol']/10000).toFixed(2)+'万':this.tickCopy[tck]['totalvol']
                // this.dataList[tck].Hold=this.tickCopy[tck]['positionvol']>10000?(this.tickCopy[tck]['positionvol']/10000).toFixed(2)+'万':this.tickCopy[tck]['positionvol']
                this.dataList[tck].Vol=this.tickCopy[tck]['totalvol']
                this.dataList[tck].Hold=this.tickCopy[tck]['positionvol']
                this.dataList[tck].TODAYOPEN=TODAYOPEN
                if(this.tickCopy[tck]['preclose']==0){
                  this.dataList[tck].diffPrice=0
                  this.dataList[tck].diffPriceRate=0
                }
              }
              if(this.tickCopy.hasOwnProperty(this.tickname)){
                this.tickPrice.bidprice=this.tickCopy[this.tickname].bidprice;
                this.tickPrice.askprice=this.tickCopy[this.tickname].askprice;
              }
            }
            console.log('在执行')
          }
          window.clearInterval(timeVal)
        },100);
        console.log(this.dataList);
        console.log(this.uid)
      })
    },
    repush(){
      this.dataList=[];
      this.getContract()
    },
    backText(res){
      this.btContent=res;
      this.showBack = true;
      setTimeout(()=>{
        this.showBack = false;
      },800)
    },
    getMultiplier(){              //获取英文代号
      console.log(this.discUrl)
      let result=[];
      this.$axios.get('http://'+this.discUrl+':8189/instruments').then(res =>{
        let instrumentNameList = res.data.data
        this.holdPositions=instrumentNameList;
      })
    },
  },
  components:{
    payy
  },
  watch:{
    tickCopyOK(newVal){
      // alert(newVal)
    },
    Tick(newVal){
      return
      let path = this.$route.path;
      path = path.split('/');
      path=path[path.length-1];
      if(path!=='selfchoose'){
        if(path!=='pay2'){
          if(path!=='market'){
            return;
          }
        }
      }
      if(this.dataList!==null){
        let tck = newVal['instrumentid'];
        if(this.dataList.hasOwnProperty(tck)){
          this.dataList[tck].newPrice=newVal['price']
          this.dataList[tck].diffPrice=(newVal['price']-newVal['settlementprice']).toFixed(this.dataList[tck].digits)
          let diffPriceRate = (newVal['price']-newVal['preclose'])/newVal['preclose'];
          if(diffPriceRate<0){
            diffPriceRate = diffPriceRate*(-1)
          }
          let NEWPRICE = this.ToMoney(newVal['price'],this.dataList[tck].digits);
          let TODAYOPEN = this.ToMoney(newVal['preclose'],this.dataList[tck].digits);
          this.dataList[tck].NEWPRICE=NEWPRICE
          this.dataList[tck].diffPriceRate=(diffPriceRate*100).toFixed(2)+'%'
          // this.dataList[tck].Vol=newVal['totalvol']>10000?(newVal['totalvol']/10000).toFixed(1)+'万':newVal['totalvol']
          // this.dataList[tck].Hold=newVal['positionvol']>10000?(newVal['positionvol']/10000).toFixed(1)+'万':newVal['positionvol']
          this.dataList[tck].Vol=newVal['totalvol']
          this.dataList[tck].Hold=newVal['positionvol']
          this.dataList[tck].TODAYOPEN=TODAYOPEN
          if(newVal['preclose']==0){
            this.dataList[tck].diffPrice=0
            this.dataList[tck].diffPriceRate=0
          }
        }
        if(this.tickname===newVal['instrumentid']){
          let tick=newVal;
          tick['price'] =  Number(tick['price']).toFixed(this.digits)
          tick['bidprice'] =  Number(tick['bidprice']).toFixed(this.digits)
          tick['bidprice2'] =  Number(tick['bidprice2']).toFixed(this.digits)
          tick['bidprice3'] =  Number(tick['bidprice3']).toFixed(this.digits)
          tick['bidprice4'] =  Number(tick['bidprice4']).toFixed(this.digits)
          tick['bidprice5'] =  Number(tick['bidprice5']).toFixed(this.digits)
          tick['askprice'] =  Number(tick['askprice']).toFixed(this.digits)
          tick['askprice2'] =  Number(tick['askprice']).toFixed(this.digits)
          tick['askprice3'] =  Number(tick['askprice']).toFixed(this.digits)
          tick['askprice4'] =  Number(tick['askprice']).toFixed(this.digits)
          tick['askprice5'] =  Number(tick['askprice']).toFixed(this.digits)
          this.tickPrice=tick;
        };
      }
      },
    reloadSelf(){
      this.$forceUpdate();
    }
  }
}
</script>
<style lang="scss" scoped>
  @import "../../style/theme2";
  .fade-enter{
    transform: translateX(tovw(375));
    opacity: 0;
  }
  .fade-enter-to{
    transform: translateX(0px);
    opacity: 1;
  }
  .fade-leave-to{
    transform: translateX(tovw(375));
    opacity: 0;
  }
  .fade-enter-active{
    transition:all 0.4s ease
  }
  .fade-leave-active{
    transition:all 0.3s ease
  }
  //
  .pay-enter{
    transform: translateY(tovw(240));
    opacity: 0;
  }
  .pay-enter-to{
    transform: translateY(0px);
    opacity: 1;
  }
  .pay-leave-to{
    transform: translateY(tovw(240));
    opacity: 0;
  }
  .pay-enter-active{
    transition:all 0.4s ease
  }
  .pay-leave-active{
    transition:all 0.3s ease
  }
.selfchoose {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: absolute;
  .scheader {
    font-size: tovw(14);
    height: tovw(28);
    line-height: tovw(28);
    color: #8C8B8B;
    background-color: #FFF;
    border-bottom: tovw(1) solid #D5D3D3;
    text-align: left;
    span {
      display: inline-block;
    }
    .scheader1 {
      width: tovw(120);
      padding-left: tovw(16);
      /*padding-right:tovw(30);*/
      margin-right: tovw(25);
      line-height: tovw(30);
      text-align: center;
    }
    .scheader2 {
      margin-right:tovw(43);
      line-height: tovw(30);
      width: tovw(78);
      text-align: right;
    }
    .scheader3 {
      width: tovw(50);
      text-align: center;
    }
    .scheader4 {
      font-family: 'iconfont'!important;
    }
  }
  .scroll {
    position: absolute;
    top: tovw(28);
    width: 100%;
    bottom: tovw(0);
    overflow: auto;
    .add {
      width: 100%;
      height: tovw(40);
      font-size: tovw(16);
      color: #2E8CFF;
      line-height: tovw(40);
      background-color: #FFF;
      text-align: center;
    }
  }
  .scroll::-webkit-scrollbar {
    display: none;
  }
  .backTip {
    width: 100%;
    position: absolute;
    top: 40%;
    left: 0;
    text-align: center;
    .bt-content{
      display: inline-block;
      color: #2F2F2F;
      font-size: tovw(14);
      padding: tovw(7) tovw(15) tovw(7) tovw(15);
      background-color: #DFDFDF;
      border-radius: tovw(6);
      text-align: center;
      max-width: tovw(200);
    }

  }
  .item {
    width: 100%;
    height: tovw(50);
    background-color: #FFFFFF;
    border-bottom: tovw(1) solid #f4f4f4;
    padding: tovw(4)  tovw(0) tovw(4) tovw(16);
    font-size: 0;
    position: relative;
    span {
      display: block;
    }
    .item-name {
      display: inline-block;
      width: tovw(94);
      text-align: center;
      /*padding-right: tovw(26);*/
      .name {
        font-size: tovw(16);
        color: #4F4F4F;
      }
      .ename {
        font-size: tovw(7);
        color: #8C8B8B;
      }
    }
    .item-price {
      display: inline-block;
      color: #000;
      width: tovw(117);
      text-align: right;
      /*padding-right: tovw(74);*/
      .price1 {
        font-size: tovw(16);
        font-family: 'iconfont'!important;
        &.changeRed {
          color:#FE0000;
        }
        &.changeGre {
          color:#25B100;
        }
      }
      .price2 {
        font-size: tovw(11);
        &.changeRed {
          color:#FE0000;
        }
        &.changeGre {
          color:#25B100;
        }
      }
    }
    .item-vol {
      display: inline-block;
      padding: tovw(6) 0 tovw(6) 0;
      font-size: tovw(13);
      color: #FFF;
      width: tovw(70);
      text-align: center;
      line-height:tovw(13);
      /*vertical-align: top;*/
      position: absolute;
      right: tovw(41);
      top: tovw(12);
      height: tovw(25);
      background-color: #a5a5a5;
      border-radius: tovw(7);
      &.changeRed {
        background-color:#FE0000;
      }
      &.changeGre {
        background-color:#25B100;
      }
    }
    .item-pay {
      display: inline-block;
      .pay {
        display: none;
        width: tovw(70);
        padding: tovw(3) tovw(6);
        background-color: #FF7100;
        height: tovw(20);
        /*line-height: tovw(20);*/
        font-size: tovw(14);
        border-radius: tovw(8);
        text-align: center;
        position: absolute;
        top: 50%;
        margin-top: tovw(-10);
        right: tovw(16);
        display: flex;
        align-items: center;

        &>span {
          color: #FFFFFF;
          height: tovw(14);
          line-height:tovw(14);
        }
      }
    }
  }
  .loading {
    z-index: 5;
    width: 100%;
    position: absolute;
    top: tovw(30);
    /*bottom: tovw(56);*/
    bottom: tovw(0);
    background-color: #FFF;
    padding-top: 50%;
    text-align: center;
    .van-loading__text {
      margin-left: tovw(20);
    }
  }
}
</style>



