    handdleMsgq() {       //获取初始持仓单
      let this_ = this;
      if (this_.global.ws && this_.global.ws.readyState === 1) {
        this_.global.ws.onopen = this_.onopenq()
      }
      this_.global.ws.onmessage = function(res) {
        console.log(res)
        if((JSON.parse(res.data))['action']==='notifitrade'){
          if(this_.$store.state.changeSave=='false'){
            this_.$store.commit('changeSave','true')
          }else{
            this_.$store.commit('changeSave','false')
          }
        }
        this_.websocketonmessageq(res)
      };
    },
    onopenq () { // websocket连接后发送数据(send发送)
      let actions,balance;
      let this_ = this
      actions = {
        action: "loadposition",
        reqID: 45,
        data: {
          userId : this_.uid,
        },
      }
      this_.global.ws.send(JSON.stringify(actions))
      console.log(JSON.stringify(actions))
    },
    websocketonmessageq (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      let this_=this;
      console.log(this.redata)
      if(this.redata.action==='instrumentChange'){
        this.getContract2();
        this.$emit('DataList',this.dataList);
      }
      if(this_.redata.data.code === 0 && this_.redata.data.object != null){
        let redata=this_.redata.data.object;
        let list={};
        for(let i=0;i<redata.length;i++){
          if(list[redata[i]['instrumentid']]){
            list[redata[i]['instrumentid']]=list[redata[i]['instrumentid']]+redata[i]['volume']-redata[i]['volumeFrozen']-redata[i]['volumeClosed'];
          }else {
            list[redata[i]['instrumentid']]=redata[i]['volume']-redata[i]['volumeFrozen']-redata[i]['volumeClosed'];
          }
          console.log(list[redata[i]['instrumentid']])
        }
        for(let tck in this_.mainList){
          this_.$set(this_.mainList[tck],'save',0);
        }
        for(let tck in list){
          if(this_.mainList.hasOwnProperty(tck)){
            this_.$set(this_.mainList[tck],'save',list[tck]);
          }
        };
      }
      if(this.redata.action === "documentary" ){
        if(this.redata.data.object.code === 0){
          Toast({
            duration:1500,
            position:'bottom',
            message:'跟单成功'
          })
        }else {
          this_.followState = false
          Toast({
            duration:2000,
            position:'bottom',
            message:this_.redata.data.object.msg+'，已取消跟单',
          })
        }
      }
    },
  },
  watch:{
      Tick: {
        handler(newVal){
         this.tickCopy2= JSON.parse(localStorage.tickCopy);
        // console.log(1)
        // console.log(this.tickCopy2)
        for(let tck in this.tickCopy2){
          if(this.mainList.hasOwnProperty(tck)){
            let name =tck;
            this.mainList[name].newPrice=this.tickCopy2[tck]['price']
            this.mainList[name].diffPrice=(this.tickCopy2[tck]['price']-this.tickCopy2[tck]['preclose']).toFixed(this.mainList[name].digits)
            let diffPriceRate = (this.tickCopy2[tck]['price']-this.tickCopy2[tck]['preclose'])/this.tickCopy2[tck]['preclose'];
            if(diffPriceRate<0){
              diffPriceRate = diffPriceRate*(-1)
            }
            let NEWPRICE = this.ToMoney(this.tickCopy2[tck]['price'],this.mainList[name].digits);
            let TODAYOPEN = this.ToMoney(this.tickCopy2[tck]['preclose'],this.mainList[name].digits);
            this.mainList[name].NEWPRICE=NEWPRICE;
             if(diffPriceRate*100<0.01&&diffPriceRate*100!==0){
                   this.mainList[name].diffPriceRate='0.01%'
                }else{
                 this.mainList[name].diffPriceRate=(diffPriceRate*100).toFixed(2)+'%'
                }

            // this.mainList[name].Vol=this.tickCopy2[tck]['totalvol']>10000?(this.tickCopy2[tck]['totalvol']/10000).toFixed(2)+'万':this.tickCopy2[tck]['totalvol']
            // this.mainList[name].Hold=this.tickCopy2[tck]['positionvol']>10000?(this.tickCopy2[tck]['positionvol']/10000).toFixed(2)+'万':this.tickCopy2[tck]['positionvol']
            this.mainList[name].Vol=this.tickCopy2[tck]['totalvol']
            this.mainList[name].Hold=this.tickCopy2[tck]['positionvol']
            this.mainList[name].TODAYOPEN=TODAYOPEN
            if(this.tickCopy2[tck]['preclose']==0){
              // this.mainList[name].diffPrice=(this.tickCopy2[tck]['price']-this.tickCopy2[tck]['settlementprice']).toFixed(this.mainList[name].digits);
              // if(Math.abs(this.mainList[name].diffPrice)/this.tickCopy2[tck]['settlementprice']*100<0.01){
                //  this.mainList[name].diffPriceRate='0.01%'
              // }else {
              // this.mainList[name].diffPriceRate=(Math.abs(this.mainList[name].diffPrice)/this.tickCopy2[tck]['settlementprice']*100).toFixed(2)+'%';
              // }

              this.mainList[name].diffPrice=0
              this.mainList[name].diffPriceRate='0%';
            }
          }
        };

        },
        deep: true
      },
  }
}
</script>
